<script lang='ts'>
	import { writable } from 'svelte/store';
	import type { CSS } from '../../resources/utilities';

	import FillOrTwoToneButton from '../blocks/buttons/FillOrTwoToneButton.svelte';
	import Fragment from '../blocks/Fragment.svelte';
	import Logo from '../blocks/Logo.svelte';

	export let isTitlesEnabled = true;
	export let isTitlesEnabledW = writable(isTitlesEnabled);

	const IconAndTitleMap = {
		cottage: 'Home',
		tag: 'These',
		notifications: 'Aren\'t',
		mail: 'Actually',
		bookmark: 'Functional',
		subject: 'They\'re',
		account_circle: 'Purely',
		more_vert: 'Distractions',
	};
	const IconAndTitleEntries = Object.entries(IconAndTitleMap);
	const buttonContainers: HTMLDivElement[] = [];

	const textColour: CSS = '--colour-text-primary';
</script>

<Fragment
	isPadded={true}
	isInAnimated={true}
	isOutAnimated={true}
	padding='calc(var(--padding) / 2)'
	height='100vh'
	width='100%'
	justify='flex-start'
	rows={`repeat(${
		// + logo
		IconAndTitleEntries.length + 1
	}, 56px)`}
>
	<Logo
		colour='--colour-text-primary'
		height='auto'
		width='auto'
	/>
	{#each IconAndTitleEntries as [icon, title], i}
		<container
			class='button'
			bind:this={buttonContainers[i]}
		>
			<FillOrTwoToneButton
				backgroundColour='transparent'
				hoverColour='--colour-accent-secondary'
				{icon}
				on:mousein={() => {
					buttonContainers[i]
				}}
			>
				{#if $isTitlesEnabledW}
					<heading>
						{title}
					</heading>
				{/if}
			</FillOrTwoToneButton>
		</container>
	{/each}
</Fragment>

<style>
	heading {
		font-size: 1rem;
	}

	container.button:hover * {
		color: var(--colour-accent-primary);
	}
</style>